html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}header{display:flex;justify-content:center}button{margin:0;padding:0;border:none}button:focus{outline:none}button:hover{cursor:pointer}#title{display:block}.wiggle{position:relative;background-image:-webkit-linear-gradient(#82ce8c 0%, #b8f1b6 46%, #fff 50%, #12320e 54%, #f1b6e8 58%, #b16692 90%, #9c7293 100%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-text-stroke:4px #f5f5f5;font-size:90px;font-family:'Titillium Web', sans-serif;font-style:italic;margin:0;line-height:1.2}.wiggle:before{content:'WIGGLE';position:absolute;left:0;top:0;z-index:10;background-image:-webkit-linear-gradient(-40deg, transparent 0%, transparent 40%, #fff 50%, transparent 60%, transparent 100%);background-position:-250px 0;-webkit-background-clip:text;-webkit-text-stroke:0;padding-right:100px;-webkit-animation-name:wiggle_effect;-webkit-animation-duration:6s;-webkit-animation-delay:2s;-webkit-animation-timing-function:linear;-webkit-animation-iteration-count:infinite}.animals{position:absolute;margin:0px;font-family:'Mr Dafoe', cursive;font-size:70px;color:#2b6e36;transform:rotate(-15deg);-ms-transform:rotate(-15deg);-webkit-transform:rotate(-15deg);margin-left:155px;margin-top:-50px;-webkit-text-stroke:1px #4b8858;-webkit-filter:drop-shadow(2px 2px 20px #2e7252);z-index:20}.windows .animals{-webkit-text-stroke:4px #2c6139}@-webkit-keyframes wiggle_effect{0%{background-position:-120px 0}10%{background-position:160px 0}100%{background-position:160px 0}}#penguin,#monkey,#hippo,#lion,#koba,#pickle,#cow{border-radius:50%;display:inline-block}.buttons{margin-top:100px}.modal{display:none;position:fixed;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:#34794e;background-color:rgba(52,121,78,0.4)}.modal-content{background-color:#fefefe;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:20px;margin:10% auto;padding:20px;border:1px solid #888;width:50%}.closeModal{color:#aaa;float:right;font-size:28px;font-weight:bold}.closeModal:hover,.closeModal:focus{color:black;text-decoration:none;cursor:pointer}
